.dados_dep{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.dados_dep figcaption{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.dados_dep img{
    width: 200px;
    height: 200px;
    object-fit: cover;
    object-position: center;
    border-radius: 60%;
}
.dados_dep p{
    width: 70%;
    font-size: 14pt;
    text-align: center;
}
.dados_dep figcaption p,.dados_dep img{
    margin-bottom: 10px;
}
@media (max-width: 600px){
    .dados_dep p{
        font-size: 12pt;
    }
    .dados_dep img{
        width: 170px;
        height: 170px;
    }
}
