.MenuHamburgue{
    height: 100%;
    width: 20%;
    position: relative;
    display: flex;
    display: none;
    align-items: center;
    justify-content: center;
}
.MenuHamburgue img{
    width: 25px;
    cursor: pointer;
}
.MenuHamburgue nav{
    height: 100vh;
    width: 200px;
    background-color:#2c4274;
    position:absolute;
    right: 0;
    top: 100%;
    animation: botao_menu 1s;
}
@keyframes botao_menu{
    0%{
        transform: translateX(50%);
        opacity: 0.1;
    }
    100%{
        transform: translateX(0%);
        opacity: 1;
    }
}
.MenuHamburgue nav ul{
    list-style: none;
    height: 10%;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-around;
}
.MenuHamburgue nav a{
    color: #f1f1f1;
    text-decoration: none;
}
.MenuHamburgue nav li{
    width: 100%;
    font-size: 14pt;
    display: flex;
    padding: 10px;
    justify-content: center;
}
.MenuHamburgue nav li:hover{
    font-size: 15pt;
    background-color: rgba(0, 0, 0, 0.5);
}
nav.menu_true{
    display: block;
}
nav.menu_false{
    display: none;
}
@media (max-width:800px){
    .MenuHamburgue{
        display: flex;
    }
}
