.depoimentos_clients{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}
.depoimentos_clients h2{
    text-align: center;
    font-size: 30pt;
    color: #C59913;
    margin-top: 20px;
    margin-bottom: 50px;
}
.depoimentos_clients span{
    color:rgb(126, 98, 8);
}
.comentarios_clients{
    display: flex;
    justify-content: center;
    flex-flow: row wrap;
    padding: 10px;
}
.depoimentos_clients a{
    color: #ffff;
    margin: 25px 0px;
    padding: 15px 20px;
    border-radius: 5px;
    background-color: rgb(9%, 12%, 25%);
    text-decoration: none;   
}

@media (max-width: 600px){
    .depoimentos_clients h2{
        font-size: 20pt;
    }
    .comentarios_clients p{
        font-size: 12pt;
    }
}
