.item{
    width: calc(100% / 2);
    display: flex;
    justify-content: center;
    align-items: center;
}
.item p{
    color: #f1f1f1;
    font-size: 16pt;
}
.item img{
    margin-right: 5px;
}
@media screen and (max-width: 600px){
    .item{
        display: flex;
        flex-direction: column;
    }
    .item img{
        margin-bottom: 10px;
    }
    .item p{
        font-size: 12pt;
    }
}