header{
    width: 100%;
    max-width: 1440px;
    height: 7vh;
    margin-bottom: 3px;
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    background-color: #1B3675;
    justify-content: space-between;
}
header h1{
    color: #f1f1f1;
}
header figure{
    display: flex;
}
header img{
    margin: 0 10px 0 10px;
}
header .menu-extendido{
    width: 20%;
    margin: 10px;
    display: none;
}
.menu-extendido ul{
    display: flex;
    align-items: center;
    justify-content: center;
    justify-content: space-evenly;
    list-style: none;
}
.menu-extendido li a{
    color: #f1f1f1;
    font-size: 18px;
    text-decoration: none;
}
@media (min-width: 800px){
    header .menu-extendido{
        display: block;
    }
}
