.medicos{
    padding: 10px;
    text-align: center;
}
.medicos img,.medicos p{
    margin-bottom: 10px;
}
.medicos img{
    width: 250px;
    height: 250px;
    object-fit: cover;
    border-radius: 50%;
}
.medicos p{
    font-size: 14pt;
}
.secao-medicos{
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
}
@media (max-width:600px){
    .medicos p{
        font-size: 12pt;
    }
    .medicos img{
        width: 170px;
        height: 170px;
    }
}