.itemVantagem{
    margin-bottom: 15px;
    width: 400px;
    display: flex;
    flex-direction: column;
}
.itemVantagem h3{
    margin-bottom:10px;
    font-size: 18pt;
}
.itemVantagem p{
    font-size: 14pt;
}
@media (max-width: 600px){
    .itemVantagem h3{
        font-size: 15pt;
    }
    .itemVantagem p{
        font-size: 12pt;
    }
}