.segunda-secao{
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}
.segunda-secao img{
    width: 50%;
    min-width: 300px;
}
.segunda-secao h2{
    padding: 20px;
    font-size: 30pt;
    color:#ce9e0d;
}
.segunda-secao span{
    color: rgb(126, 98, 8);
}
.itens-vantagens{
    width: 100%;
    margin: 50px 0px;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
}

@media (max-width: 600px){
    .segunda-secao h2{
        font-size: 20pt;
    }
    .itens-vantagens{
        margin-bottom: 30px;
    }
}
