.primeira-secao{
    height: 40vh;

    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-around;
    background-color: #1B3675;
}
.itens{
    width: 80%;
    max-width: 800px;
    height: 50%;
    display: flex;
    flex-flow: row wrap;
}
.primeira-secao hgroup{
    line-height: 45px;
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.primeira-secao hgroup h2{
    color: #C59913;
    font-size: 30pt;
}
.primeira-secao hgroup p{
    color: #f1f1f1;
    font-size: 17pt;
}
.primeira-secao hgroup span{
    color: rgb(126, 98, 8);
}
@media (max-width:810px){
    .primeira-secao hgroup h2{
        font-size: 20pt;
    }
    .primeira-secao hgroup p{
        font-size: 12pt;
    }
}
@media (max-width:600px){
    .primeira-secao hgroup h2,.primeira-secao hgroup p{
        text-align: center;
        width: 90%;
    }
}