.titulos_paragrafo{
    margin: 50px 0;
    line-height: 45px;
    text-align: center;
}
.titulos_paragrafo h2{
    color:#C59913;
    font-size: 30pt;
}
.titulos_paragrafo span{
    color:rgb(126, 98, 8);
}
.titulos_paragrafo a{
    color: rgb(16, 15, 15);
    text-decoration: none;
}
.titulos_paragrafo a:hover{
    text-decoration: underline;
}
.titulos_paragrafo p{
    font-size: 14pt;
}
@media (max-width:600px){
    .titulos_paragrafo h2{
        font-size: 20pt;
    }
    .titulos_paragrafo p{
        font-size: 12pt;
    }
}